<template>
  <el-form inline :model="queryForm">
    <el-form-item label="">
      <select-tree v-model="queryForm.tree" :options="options" />
    </el-form-item>
    <el-form-item label="">
      <select-pane
        v-model="queryForm.pane"
        url="enumCode/selectPane"
        label=""
        title=""
        :col-width="200"
        :col-titles="['', '', '', '', '']" />
    </el-form-item>
  </el-form>
</template>

<script>
import SelectPane from '@/components/SelectPane';
import SelectTree from '@/components/SelectTree';

export default {
  name: 'Select',
  components: {
    SelectPane,
    SelectTree,
  },
  data() {
    return {
      queryForm: {            // 
        pane: '',        // 
        tree: '',           // 
      },
      options: [],
    };
  },
};
</script>

<style lang="scss">

</style>



