export const basicInfoRules = {
  cName: [
    { required: true, message: '', trigger: 'blur' },
  ],
  userNO: [
    { required: true, message: '', trigger: 'blur' },
  ],
  domainId: [
    { required: true, message: '', trigger: 'change' },
  ],
  idCard: [
    { required: true, message: 'ID', trigger: 'blur' },
  ],
};

export default {
  basicInfoRules,
};



