<script>
import { basicInfoRules } from '../rules';

export default {
  model: {
    prop: 'rowId',
    event: 'setRowId',
  },
  props: ['rowId'],
  data() {
    return {
      loading: false,
      itemModel: {},
      rules: basicInfoRules,
    };
  },
  created() {
    if (this.rowId) {
      this.getItemOne();
    } else {
      this.itemModel = this.getEmptyItem();
    }
  },
  methods: {
    getItemOne() {
      this.loading = true;
      this.$http.get(`api/person/${this.rowId}`).then((response) => {
        if (response.status === 200) {
          this.itemModel = Object.assign(this.getEmptyItem(), response.data);
        } else {
          this.$message.warning({
            message: response.body.message,
            duration: 1500,
          });
        }
      }, ({ response }) => {
        this.$hp.resp(response);
      }).finally(() => {
        this.loading = false;
      });
    },
    onSaveItem() {
      this.$refs.form.validate((valid) => {
        if (!valid) return;
        this.$http[this.rowId ? 'put' : 'post']('api/person', this.itemModel)
          .then((response) => {
            if (response.status === 200) {
              this.$message.success({
                message: `${this.rowId ? '' : ''}`,
                duration: 1500,
                showClose: true,
              });
              this.itemModel = response.data;
              this.$emit('setRowId', this.itemModel.id);
              this.$emit('query');
            } else {
              this.$message.warning({
                message: response.data.message,
                duration: 1500,
              });
            }
          }, ({ response }) => {
            this.$hp.resp(response);
          });
      });
    },
    getEmptyItem() {
      return {
        cName: '',               // 
        userNO: '',              // 
        domainId: '',            // 
        quarters: '',            // 
        eName: '',               // 
        birthDay: '',            // 
        email: '',               // 
        sex: 1,                  // 
        idCard: '',              // 
        nation: '',              // 
        orderNum: '',            // 
        degree: '',              // 
        highestDegree: '',       // 
        school: '',              // 
        specialty: '',           // 
        mobile: '',              // 
        homeTel: '',             // 
        birthPlace: '',          // 
        homeAddress: '',         // 
        technicalTitle: '',      // 
        technicalTitleDate: '',  // 
        technologyNO: '',        // 
        certificateNO: '',       // 
        certificateDate: '',     // 
        yearsInThePosition: '',  // 
        joinCompanyTime: '',     // 
        workStartTime: '',       // 
        leaveCompanyTime: '',    // 
        domainIdArr: [],         // 
        tecCompetence: '',       // 
        remark: '',              // 
      };
    },
  },
};
</script>



